import React, { useState } from 'react';
import { Redirect, Route } from 'react-router-dom';
import {
	IonApp,
	IonIcon,
	IonLabel,
	IonRouterOutlet,
	IonTabBar,
	IonTabButton,
	IonTabs
} from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { ellipse, square, triangle, checkbox, create } from 'ionicons/icons';
import Tab1 from './pages/Tab1';
import Tab2 from './pages/Tab2';
import Tab3 from './pages/Tab3';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';

function today() {
	const today = new Date()
	const dd = String(today.getDate()).padStart(2, '0')
	const mm = String(today.getMonth() + 1).padStart(2, '0')
	const yyyy = today.getFullYear()

	return `${yyyy}-${mm}-${dd}`
}

function getJsonItem(key) {
	const val = localStorage.getItem(key)
	return (val ? JSON.parse(val) : val)
}

function setJsonItem(key, val) {
	return localStorage.setItem(key, JSON.stringify(val))
}

let timer
const App = () => {
	const [habits, setHabits] = useState(getJsonItem('habits') || ['Gym', 'Dishes', ''])
	const [checks, setChecks] = useState(getJsonItem('checks') || { today: today(), vals: {} })
	
	function setHabit(oldValue, newValue) {
		const newHabits = habits
			.map(i => i === oldValue ? newValue : i)
			.filter(i => i)
		newHabits.push('')
		setJsonItem('habits', newHabits)
		setHabits(newHabits)
	}

	if (!timer) {
		function updateChecks() {
			console.log(Date(), 'checking')
			const _today = today()
			if (_today !== checks.today) {
				const newChecks = { today: _today, vals: {} }
				setJsonItem('checks', newChecks)
				setChecks(newChecks)
			}
		}

		timer = setInterval(updateChecks, 10000)
	}

	function setChecked(str, value) {
		console.log(str, value)
		console.log(checks)
		const newChecks = Object.assign({}, checks)
		newChecks.vals = Object.assign({}, newChecks.vals, { [str]: value })
		setJsonItem('checks', newChecks)
		setChecks(newChecks)
	}

	return (
		<IonApp>
			<IonReactRouter>
				<IonTabs>
					<IonRouterOutlet>
						<Route
							path="/habz/tab1"
							render={routeProps => <Tab1 {...routeProps} habits={habits} setHabit={setHabit}/>}
							exact={true}
						/>
						<Route
							path="/habz/tab2"
							render={routeProps => <Tab2 {...routeProps} habits={habits} checks={checks} setChecked={setChecked}/>}
							exact={true}
						/>
						<Route path="/habz/tab3" component={Tab3} />
						<Route path="/" render={() => <Redirect to="/habz/tab2" />} exact={true} />
					</IonRouterOutlet>
					<IonTabBar slot="bottom">
						<IonTabButton tab="tab2" href="/habz/tab2">
							<IonIcon icon={checkbox} />
							<IonLabel>Track</IonLabel>
						</IonTabButton>
						<IonTabButton tab="tab1" href="/habz/tab1">
							<IonIcon icon={create} />
							<IonLabel>Edit</IonLabel>
						</IonTabButton>
						{/*<IonTabButton tab="tab3" href="/tab3">
							<IonIcon icon={square} />
							<IonLabel>Tab 3</IonLabel>
						</IonTabButton>*/}
					</IonTabBar>
				</IonTabs>
			</IonReactRouter>
		</IonApp>
	)
};

export default App;
